import React, { Component } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import { connect } from "react-redux";
import { Online, Offline } from "react-detect-offline";
import GuestRoute from "./auth/GuestRoute";
import AuthRoute from "./auth/AuthRoute";
import SignIn from "./auth/SignIn";
import Home from "./Home";
import Default from "./Default";
import "./App.css";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false,
			msg: "",
		};
	}

	openImage = () => {
		console.log("clicked");
		const value = navigator.mozWifiManager.enabled;

		this.setState({ show: true, msg: value });
	};

	render() {
		return (
			<>
				<Online>
					<div style={{ textAlign: "center" }}>
						<h4 style={{ fontWeight: "bold" }}>Home Screen</h4>

						<button
							className="btn-red"
							onClick={() => this.openImage()}
						>
							Open
						</button>

						{this.state.show ? <div>{this.state.msg}</div> : ""}
					</div>
					{/* <BrowserRouter basename="/jazzdrive-kaios">
						<Switch>
							<GuestRoute
								path="/signin"
								exact
								component={SignIn}
							/>
							<AuthRoute path="/" exact component={Home} />
							<Route component={Default} />
						</Switch>
					</BrowserRouter> */}
				</Online>
				<Offline>
					<div style={{ textAlign: "center" }}>
						<h4 style={{ fontWeight: "bold" }}>
							No Internet Connection
						</h4>
						<p>Use Wi-Fi or mobile data to access the internet.</p>
						<button className="btn-red">Settings</button>
					</div>
				</Offline>
			</>
		);
	}
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(App);
